<template>
  <div class="upload-confirm">
    <div class="upload-confirm-header">
      上传确认
      <span>单次最多可上传9张图片</span>
    </div>
    <div class="nav-method">
      <div class="nav-name">
        图片直播
      </div>
      <div class="nav-icon">
        <img src="../../assets/img/staffOnly/btn_downdrag.png" alt="">
      </div>
    </div>
    <div class="upload-photos-list">
      <div class="upload-photos-item" v-for="(item,index) in imgUrlList" :key="index">
        <div class="upload-photos-img">
          <img :src="item.url" alt="">
        </div>
        <div class="upload-photos-text">
          <div class="upload-photos-name ellipse">
            {{item.name}}
          </div>
          <div class="delete-upload-photos" @click="delPhotos(index)">
            <img src="../../assets/img/staffOnly/btn_delete.png" alt="">
            <span>删除</span>
          </div>
        </div>
      </div>
      <div class="continue-upload-btn">
        <img src="../../assets/img/contactUs/btn_update_pic_busicard.png" alt="">
        <span>继续上传</span>
        <input type="file" class="input-file" @change="changeImage($event)" accept="image/gif,image/jpeg,image/jpg,image/png" multiple>
      </div>
    </div>

    <div class="footer-box"></div>
    <div class="footer">
      <div class="upload-photos-btn" @click="uploadImg">
        确定上传
      </div>
    </div>
    <div class="footer-picker wrapper" >
      <mt-picker :slots="slots" :visible-item-count="5" :show-toolbar="true" ref="picker" value-key="name">
        <mt-button @click="handleConfirm" class="sure">确认</mt-button>
      </mt-picker>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      imgUrlList: [],
      file: [],
      type: 'spot',  //类型
      slots: [
        {
          flex: 1,
          values: [{ val: 'scene', name: '现场直播' }, { val: 'spot', name: '展台亮点' }],
          className: 'slot1',
          // textAlign: 'right'
        },
      ]
    }
  },
  methods: {
    // onValuesChange(picker, values) {
    //   // if (values[0] > values[1]) {
    //   //   picker.setSlotValue(1, values[0]);
    //   // }
    //   console.log(picker, values);

    // },
    handleConfirm() {
      console.log(this.$refs.picker.getValues()[0]);

      // this.currentTags = this.$refs.picker.getValues()[0]
      // this.popupVisible = false
    },
    delPhotos(index) {
      this.imgUrlList.splice(index, 1);
      this.file.splice(index, 1);
    },
    changeImage(e) {  //获取图片

      let file = e.target.files;
      if (this.imgUrlList.length > 9) {
        this.$zero.Toast('选择失败：单次最多可上传9张图片');
        return;
      }
      let files = [];
      if (this.imgUrlList.length > 0) {
        for (let i = 0; i < file.length; i++) {
          let isPush = true;
          for (let j = 0; j < this.imgUrlList.length; j++) {
            if (this.imgUrlList[j].name === file[i].name) {
              isPush = false;
            }
          }
          if (isPush) {
            files.push(file[i])
          }
        }
        this.imgHandle(files)
      } else {
        this.imgHandle(file)
      }

    },
    imgHandle(file) {

      for (let i = 0; i < file.length; i++) {
        // console.log(file[i]);

        let item = {
          url: this.$zero.fileToBlobUrl(file[i]),
          name: file[i].name,
          show: 1
        }

        this.imgUrlList.push(item)
        this.file.push(file[i])
      }

    },
    async uploadImg() {
      if (this.imgUrlList.length === 0) {
        this.$zero.Toast('请上传图片');
        return;
      }
      try {
        const formData = new FormData();

        for (let i = 0; i < this.file.length; i++) {
          formData.append('file', this.file[i]);
        }

        let uploadImgRes = await this.$api.files.uploadImg({
          file: formData
        })

        if (uploadImgRes.statusCode !== 75200 && uploadImgRes.statusCode !== 75600) {
          this.$zero.Toast('服务器异常，请稍后重试');
          return;
        }

        let imgList = [];
        if (uploadImgRes.imgStatus) {
          let imgStatus = uploadImgRes.imgStatus;

          for (let i = 0; i < imgStatus.length; i++) {

            let item = {
              url: imgStatus[i].server_name,
              name: imgStatus[i].upload_name,
              show: 1
            }
            imgList.push(item);
          }
        } else {

          let item = {
            url: uploadImgRes.server_name,
            name: uploadImgRes.upload_name,
            show: 1
          }

          imgList.push(item);

        }


        let res = await this.$api.staffOnly.postImg({
          exhibitionID: '',
          img: imgList,
          type: this.type
        })
        console.log(res);

        if (res.statusCode === 75200) {
          this.$router.push('/staffWorkbench')
        }else {
          this.$zero.Toast('服务器异常，请稍后重试');
        }

      } catch (err) {
        this.$zero.Toast('服务器异常，请稍后重试');
      }
    }
  },
  created() {
    if (this.$route.params.length > 0) {
      this.imgHandle(this.$route.params)
    }
  },
}
</script>
<style>
.upload-confirm {
  width: 100%;
  height: 100%;
  overflow: auto;
  background: url("../../assets/img/contactUs/bg_popup.png") no-repeat;
  background-size: 100% 100%;
}
