  padding: 0.6rem 0.2rem 0.3rem 0.4rem;
  color: #fff;
  font-size: 0.36rem;
  font-weight: bold;
  /* border-bottom: 1px solid #346b83; */
}
.upload-confirm-header > span {
  font-size: 0.24rem;
  color: #99b5c2;
  margin-left: 0.3rem;
}
