  font-size: 0.24rem;
  color: #ff5555;
  display: flex;
  align-items: center;
}
.delete-upload-photos > img {
  width: 0.25rem;
  height: 0.25rem;
  margin-right: 0.05rem;
}
.continue-upload-btn {
  width: 100%;
  border: 1px solid #00638f;
  box-sizing: border-box;
  padding: 0.32rem 0;
