}
.upload-confirm .nav-method {
  display: flex;
  justify-content: center;
  border-bottom: 1px solid #337488;
  position: relative;
  /* align-items: center; */
}
.upload-confirm .nav-name {
  height: 0.5rem;
  font-size: 0.3rem;
  font-weight: bold;
