  padding: 0 0.15rem;
  background: url("../../assets/img/staffOnly/bg_chosen.png") no-repeat center
    bottom;
  background-size: 110% 80%;
}
.upload-confirm .nav-icon {
  width: 0.2rem;
  height: 0.2rem;
  margin-top: 0.05rem;
}
.upload-confirm .nav-icon > img {
  width: 100%;
  height: 100%;
}

.upload-confirm .footer-picker {
  width: 100%;
  height: 100%;
  display: none;
  /* display: flex;
  flex-direction: column;
  justify-content: flex-end; */
}
.picker-item{
  font-size: .26rem;
}
/* .picker-toolbar{
  position: absolute;
  
} */
.mint-button{
  background-color: transparent;
  border:0px;
  box-shadow:none;
}
